







import { Vue, Component } from 'vue-property-decorator';

@Component
export default class Cover extends Vue {
    private time = 5;

    private mounted() {
        this.interval();
    }

    private interval() {
        const timeStop = setInterval(() => {
            this.time--;
            if (this.time === 0) {
                this.$router.push('welcome');
            }
        }, 1000);
    }
}
